import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import './constants';
import {
	FETCH_INDICATOR_STARTED,
	FETCH_INDICATOR_SUCCEEDED,
	FETCH_INDICATOR_FAILED,
	CREATE_TARGET_STARTED,
	CREATE_TARGET_SUCCEEDED,
	CREATE_TARGET_FAILED,
	UPDATE_TARGET_STARTED,
	UPDATE_TARGET_SUCCEEDED,
	UPDATE_TARGET_FAILED,
	DELETE_TARGET_STARTED,
	DELETE_TARGET_SUCCEEDED,
	DELETE_TARGET_FAILED,
} from './constants';

export const indicatorQuery = `query ($queryString: String) {
	GlideRecord_Query {
	  pa_indicators(queryConditions: $queryString) {
		_results {
		  direction{
			value
		  }
		}
	  }
	}
  }`;

/**
 * GraphQl call for fetching indicator data
 */
export const fetchIndicatorData = createGraphQLEffect(indicatorQuery, {
	variableList: ['queryString'],
	startActionType: FETCH_INDICATOR_STARTED,
	successActionType: FETCH_INDICATOR_SUCCEEDED,
	errorActionType: FETCH_INDICATOR_FAILED,
});

/**
 * API call for creating a target value for the target
 * with { uuid, date, review_date, value, type }
 */
export const createTarget = createHttpEffect(
	'/api/now/pa/targets/create_value',
	{
		method: 'POST',
		dataParam: 'data',
		startActionType: CREATE_TARGET_STARTED,
		successActionType: CREATE_TARGET_SUCCEEDED,
		errorActionType: CREATE_TARGET_FAILED,
	}
);

/**
 * API call for updating a target value for the target
 * with { uuid, previous_date, date, previous_review, review_date, value, type }
 */
export const updateTarget = createHttpEffect(
	'/api/now/pa/targets/update_value',
	{
		method: 'POST',
		dataParam: 'data',
		startActionType: UPDATE_TARGET_STARTED,
		successActionType: UPDATE_TARGET_SUCCEEDED,
		errorActionType: UPDATE_TARGET_FAILED,
	}
);

/**
 * Delete a target value for the target with { uuid, date, type }
 */
export const deleteTarget = createHttpEffect(
	'/api/now/pa/targets/values/:sysId',
	{
		method: 'DELETE',
		pathParams: ['sysId'],
		startActionType: DELETE_TARGET_STARTED,
		successActionType: DELETE_TARGET_SUCCEEDED,
		errorActionType: DELETE_TARGET_FAILED,
	}
);
