import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import moment from 'moment';
import get from 'lodash/get';
import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-alert';
import '@servicenow/now-loader';
import 'sn-pa-target-form';

import styles from './sn-pa-target-form-connected.scss';
import {
	FETCH_INDICATOR_REQUESTED,
	FETCH_INDICATOR_SUCCEEDED,
	DEFAULT_TARGET_TYPE,
	CREATE_TARGET_REQUESTED,
	UPDATE_TARGET_REQUESTED,
	DELETE_TARGET_REQUESTED,
	CREATE_TARGET_SUCCEEDED,
	UPDATE_TARGET_SUCCEEDED,
	DELETE_TARGET_SUCCEEDED,
	EDIT_TARGET_DELETE_CLICKED,
	EDIT_TARGET_CANCEL_CLICKED,
	EDIT_TARGET_UPDATE_CLICKED,
	EDIT_TARGET_SAVE,
	CREATE_TARGET_CANCEL_CLICKED,
	CREATE_TARGET_SAVE,
	EDIT_TARGET_BACK_CLICKED,
	CREATE_TARGET_BACK_CLICKED,
	DELETE_TARGET_FAILED,
	UPDATE_TARGET_FAILED,
	CREATE_TARGET_FAILED,
	CREATE_TARGET_STARTED,
	UPDATE_TARGET_STARTED,
	DELETE_TARGET_STARTED,
	ERROR_OCCURRED,
} from './constants';

import {
	TARGET_DATE_FORMAT,
	TARGET_DATE_VISIBLE_FORMAT,
	TARGET_DATE,
	TARGET_REVIEW_DATE,
	TARGET_VALUE,
	TARGET_TYPE,
} from 'sn-pa-target-form/constants';
import {
	fetchIndicatorData,
	createTarget,
	updateTarget,
	deleteTarget,
} from './actions';
import {
	focusToNode,
	getDateFormatted,
	getIndicatorSysId,
	buildUuidFromTargetValue,
} from './utils';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const userLanguage = () => {
	return get(
		window,
		'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user.language',
		'en'
	);
};

const handleFailure = ({ action: { payload }, updateState, dispatch }) => {
	const message = get(payload, 'data.error.detail');

	if (message)
		updateState({
			status: undefined,
			messages: [{ message, type: 'critical' }],
		});
	dispatch(ERROR_OCCURRED, payload);
};

const getAlerts = (messages) =>
	messages.map((message, index) => (
		<now-alert
			status={message.type}
			icon="triangle-exclamation-outline"
			content={message.message}
			append-to-payload={{ type: 'dismiss', index }}
			action={{ type: 'dismiss' }}
			expanded
		></now-alert>
	));

const view = (
	{
		properties: {
			createTarget,
			uuid,
			targetValue,
			formTitle,
			showBackButton,
			baselineTargetValue,
			averageScore,
			hasCreateTargetRole,
		},
		init,
		value,
		status,
		targetAt,
		backBtnElm,
		targetType,
		targetReview,
		messages = [],
		indicatorDirection,
		hasError,
		hasNoBaselineError,
	},
	{ updateState }
) => {
	const messagesList = (
		<div>
			{!status && messages.length ? getAlerts(messages) : undefined}
		</div>
	);

	const setElementRef = (ref, name) => updateState({ [name]: ref });

	const backButton = (
		<div slot="pre-title">
			<now-button-bare
				hook-insert={({ elm }) => setElementRef(elm, 'backBtnElm')}
				hook-destroy={() => setElementRef(null, 'backBtnElm')}
				label={t('Back')}
				iconStart="chevron-left-outline"
				variant="tertiary"
				append-to-payload={{ type: CREATE_TARGET_BACK_CLICKED }}
				size="md"
				configAria={{ button: { 'aria-label': t('Back') } }}
				disabled={!!status}
			></now-button-bare>
		</div>
	);

	if (init && backBtnElm) {
		focusToNode(backBtnElm);
		updateState({ init: false });
	}

	if (createTarget || !targetValue)
		return (
			<sn-pa-target-form
				uuid={uuid}
				type={targetType}
				manage-value
				value={value}
				create-target={createTarget}
				form-title={formTitle}
				baseline-target-value={baselineTargetValue}
				average-score={averageScore}
				has-create-target-role={hasCreateTargetRole}
				indicator-direction={indicatorDirection}
				show-back-button={showBackButton}
			>
				<div slot="pre-form">
					{messagesList}
					<slot name="kpi-info" />
				</div>
				<div slot="post-form">
					{status ? <now-loader label={status.message} /> : undefined}
				</div>
				<div slot="actions">
					<div className="actions create">
						<now-button
							className="save-btn"
							size="sm"
							variant="primary"
							append-to-payload={{ type: CREATE_TARGET_SAVE }}
							disabled={
								!!status ||
								!value ||
								value === undefined ||
								!targetAt ||
								hasError ||
								hasNoBaselineError
							}
							label={t('Save')}
						/>
						<now-button
							className="cancel-btn"
							size="sm"
							variant="secondary"
							append-to-payload={{
								type: CREATE_TARGET_CANCEL_CLICKED,
							}}
							label={t('Cancel')}
							disabled={!!status}
						/>
					</div>
				</div>
				{showBackButton ? backButton : undefined}
			</sn-pa-target-form>
		);
	return (
		<sn-pa-target-form
			uuid={uuid}
			value={value}
			targetAt={targetAt}
			targetReview={targetReview}
			type={targetType}
			form-title={formTitle}
			hide-target-type
			show-back-button={showBackButton}
			has-create-target-role={hasCreateTargetRole}
		>
			<div slot="pre-form">
				{messagesList}
				<slot name="kpi-info" />
			</div>
			<div slot="post-form">
				{status ? <now-loader label={status.message} /> : undefined}
			</div>
			<div slot="actions">
				<div className="actions update">
					<now-button
						className="update-btn"
						size="sm"
						variant="primary"
						append-to-payload={{ type: EDIT_TARGET_UPDATE_CLICKED }}
						disabled={
							!!status ||
							!value ||
							value === undefined ||
							!targetAt ||
							(!hasCreateTargetRole && targetType === 'global')
						}
						label={t('Update')}
					/>
					<now-button
						className="cancel-btn"
						size="sm"
						variant="secondary"
						append-to-payload={{ type: EDIT_TARGET_CANCEL_CLICKED }}
						disabled={!!status}
						label={t('Cancel')}
					/>
					<now-button
						className="delete-btn"
						size="sm"
						variant="secondary"
						label={t('Delete')}
						disabled={
							!!status ||
							(!hasCreateTargetRole && targetType === 'global')
						}
						append-to-payload={{ type: EDIT_TARGET_DELETE_CLICKED }}
					/>
				</div>
			</div>
			{backButton}
		</sn-pa-target-form>
	);
};

createCustomElement('sn-pa-target-form-connected', {
	renderer: {
		type: snabbdom,
	},
	properties: {
		uuid: {
			schema: {
				type: 'string',
			},
		},
		targetRecord: {
			schema: {
				type: 'object',
			},
		},
		targetValue: {
			schema: {
				type: 'object',
			},
		},
		createTarget: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		targetType: {
			schema: {
				type: 'string',
			},
			default: DEFAULT_TARGET_TYPE,
		},
		openForm: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		showBackButton: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		formTitle: {
			schema: {
				type: 'string',
			},
		},
		baselineTargetValue: {
			schema: {
				type: 'integer',
			},
		},
		averageScore: {
			schema: {
				type: 'integer',
			},
		},
		hasCreateTargetRole: {
			schema: {
				type: 'boolean',
			},
		},
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({
			properties: {
				uuid,
				targetValue: { target_at, target_review, value } = {},
				targetType,
				createTarget,
				hasCreateTargetRole,
			},
			updateState,
			dispatch,
		}) => {
			if (uuid && createTarget)
				dispatch(FETCH_INDICATOR_REQUESTED, {
					queryString: `sys_id=${getIndicatorSysId(uuid)}`,
				});
			updateState({
				init: true,
				value: value === '' ? undefined : value,
				targetType: !hasCreateTargetRole && createTarget ? 'personal' : targetType,
				targetAt: getDateFormatted(target_at),
				targetReview: getDateFormatted(target_review),
			});
		},
		[TARGET_TYPE]: ({ action: { payload }, updateState }) => {
			updateState({ targetType: payload.value });
		},
		[TARGET_DATE]: ({ action: { payload }, updateState }) => {
			updateState({ targetAt: payload.value });
		},
		[TARGET_REVIEW_DATE]: ({ action: { payload }, updateState }) => {
			updateState({ targetReview: payload.value });
		},
		[TARGET_VALUE]: ({ action: { payload }, updateState }) => {
			updateState({
				value: payload.value,
				percentValue: payload.percentValue,
			});
		},
		ERROR: ({ action: { payload }, updateState }) => {
			updateState({ hasError: payload.hasError });
		},
		BASELINE_ERROR: ({ action: { payload }, updateState }) => {
			updateState({ hasNoBaselineError: payload.hasNoBaselineError });
		},
		'NOW_BUTTON#CLICKED': ({
			action: {
				payload: { type },
				stopPropagation,
			},
			dispatch,
			state: {
				properties: { uuid, targetValue },
				value,
				targetAt,
				targetReview,
				targetType,
			},
		}) => {
			if (
				[
					EDIT_TARGET_CANCEL_CLICKED,
					EDIT_TARGET_DELETE_CLICKED,
					EDIT_TARGET_SAVE,
					EDIT_TARGET_UPDATE_CLICKED,
					CREATE_TARGET_SAVE,
					CREATE_TARGET_CANCEL_CLICKED,
				].indexOf(type) > -1
			) {
				moment.locale(userLanguage());
				stopPropagation();

				switch (type) {
					case EDIT_TARGET_SAVE:
						dispatch(CREATE_TARGET_REQUESTED, {
							data: {
								uuid,
								value: value,
								date: Number(
									moment(
										targetAt,
										TARGET_DATE_VISIBLE_FORMAT
									).format(TARGET_DATE_FORMAT)
								),
								type: targetType,
								review_date: targetReview
									? Number(
											moment(
												targetReview,
												TARGET_DATE_VISIBLE_FORMAT
											).format(TARGET_DATE_FORMAT)
									  )
									: undefined,
							},
						});
						break;

					case EDIT_TARGET_UPDATE_CLICKED:
						dispatch(UPDATE_TARGET_REQUESTED, {
							data: {
								uuid: buildUuidFromTargetValue(targetValue),
								value: value,
								date: moment(
									targetAt,
									TARGET_DATE_VISIBLE_FORMAT
								).format(TARGET_DATE_FORMAT),
								type: get(targetValue, 'target.owner.sys_id')
									? 'personal'
									: 'global',
								review_date: targetReview
									? moment(
											targetReview,
											TARGET_DATE_VISIBLE_FORMAT
									  ).format(TARGET_DATE_FORMAT)
									: undefined,
								previous_date: moment(
									targetValue.target_at,
									TARGET_DATE_FORMAT
								).format(TARGET_DATE_FORMAT),
								previous_review:
									targetValue.target_review || undefined,
							},
						});
						break;

					case EDIT_TARGET_DELETE_CLICKED:
						dispatch(DELETE_TARGET_REQUESTED, {
							sysId: targetValue.sys_id,
						});
						break;

					case EDIT_TARGET_CANCEL_CLICKED:
					default:
						// Logic to move back to list when cancel is pressed.
						// Will add that on list-connected integration
						break;
				}

				dispatch(type);
			}
		},
		'NOW_BUTTON_BARE#CLICKED': ({
			action: {
				payload: { type },
				stopPropagation,
			},
			dispatch,
		}) => {
			if (
				[EDIT_TARGET_BACK_CLICKED, CREATE_TARGET_BACK_CLICKED].indexOf(
					type
				) > -1
			) {
				stopPropagation();
				dispatch(type);
			}
		},
		'NOW_ALERT#ACTION_CLICKED': ({
			action: {
				payload: { type, index },
			},
			state: { messages },
			updateState,
		}) => {
			if (type === 'dismiss') {
				const messagesRest = [...messages];
				messagesRest.splice(index, 1);
				updateState({ messages: messagesRest });
			}
		},

		[FETCH_INDICATOR_REQUESTED]: fetchIndicatorData,
		[FETCH_INDICATOR_SUCCEEDED]: ({ action: { payload }, updateState }) => {
			updateState({
				indicatorDirection: get(
					payload,
					'data.GlideRecord_Query.pa_indicators._results[0].direction.value'
				),
			});
		},
		[CREATE_TARGET_REQUESTED]: createTarget,
		[CREATE_TARGET_STARTED]: ({ updateState }) =>
			updateState({ status: { message: t('Creating target...') } }),
		[CREATE_TARGET_SUCCEEDED]: ({ updateState }) => {
			updateState({ status: undefined });
		},

		[UPDATE_TARGET_REQUESTED]: updateTarget,
		[UPDATE_TARGET_STARTED]: ({ updateState }) =>
			updateState({ status: { message: t('Updating target...') } }),
		[UPDATE_TARGET_SUCCEEDED]: ({ updateState }) => {
			updateState({ status: undefined });
		},

		[DELETE_TARGET_REQUESTED]: deleteTarget,
		[DELETE_TARGET_STARTED]: ({ updateState }) =>
			updateState({ status: { message: t('Deleting target...') } }),
		[DELETE_TARGET_SUCCEEDED]: ({ updateState }) => {
			updateState({ status: undefined });
		},
		[DELETE_TARGET_FAILED]: handleFailure,
		[UPDATE_TARGET_FAILED]: handleFailure,
		[CREATE_TARGET_FAILED]: handleFailure,
	},
	view,
	styles,
});
