import moment from 'moment';
import {
	TARGET_DATE_FORMAT,
	TARGET_DATE_VISIBLE_FORMAT,
} from 'sn-pa-target-form/constants';

export const getDateFormatted = (
	date,
	srcFormat = TARGET_DATE_FORMAT,
	format = TARGET_DATE_VISIBLE_FORMAT
) => {
	const dateMoment = moment(date, srcFormat);
	return dateMoment.isValid() && date !== 0
		? dateMoment.format(format)
		: undefined;
};

export const getIndicatorSysId = (uuid) => {
	if (uuid && typeof uuid === 'string') {
		const parts = uuid.split(':');
		return parts[0];
	}
	return '';
};

export function focusToNode(node, query) {
	let focusNode = node;

	if (query) focusNode = (node.shadowRoot || node).querySelector(query);

	if (focusNode && typeof focusNode.focus === 'function')
		setTimeout(() => focusNode.focus(), 0);
}

/**
 * Returns the uuid string
 * @param {Object} targetValue
 *
 * @returns String uuid
 */
export function buildUuidFromTargetValue(targetValue) {
	if (!targetValue || !targetValue.target) return '';

	let target = targetValue.target;

	if (!target.indicator.sys_id) return '';

	let uuid = [];
	uuid.push(target.indicator.sys_id);

	if (target.breakdown.sys_id && target.element)
		uuid.push(target.breakdown.sys_id, target.element);

	if (target.breakdown_level2.sys_id && target.element_level2)
		uuid.push(target.breakdown_level2.sys_id, target.element_level2);

	if (target.aggregate) uuid.push(target.aggregate);

	let uuidStr = uuid.join(':');
	if (targetValue.sys_domain) return `${uuidStr};${targetValue.sys_domain}`;

	return uuidStr;
}
